import React, { useCallback, useRef, useState } from "react";
import { SafeAreaView, StyleSheet } from "react-native";
import Header from "../../component/AppBar";
import { useFocusEffect } from "@react-navigation/native";
import { getModeler } from "../../api/CreateContractApi";
import { token } from "./CreateContractScreen";
import WebView from "react-native-webview";
import { WEB_VIEW_FORM } from "../../utlis/contanst";
import SplashScreen from "../../component/SplashScreen";
import { useMyContractStore } from "../../store/useMyContractStore";
import { Button, ButtonSize, View } from "react-native-ui-lib";
import { colors } from "../../../assets/color/color";

const InfoContract = (props: any) => {
  const { route, navigation } = props;
  const webview = useRef<any>(null);
  const [formVersion, setFormVersion] = useState()
  const [disable, setDisable] = useState(false)
  useFocusEffect(useCallback(() => {
    (async () => {
      const servicerSelect = (useMyContractStore.getState() as any).serviceSelected;
      const data = await getModeler(servicerSelect.modelerId, token)
      setFormVersion(data.formVersion)
    })()
  }, []))

  const onMessage = (event: any) => {
    const res = JSON.parse(event.nativeEvent.data);
    if (res.result == 'result') {
      console.log("res.data", res.data);
      navigation.navigate('createContractSuccess');
    }
  }

  const handleSubmitForm = () => {
    const data = JSON.stringify(
      {
        result: "submit",
      }
    )
    const scriptJSX = `window.postMessage('${data}', '*');`;
    webview?.current?.injectJavaScript(scriptJSX)
  }
  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: "#fff" }}>
      <Header label='To h s' {...props} />
      {formVersion && (
        <>
          <WebView
            ref={webview}
            startInLoadingState
            renderLoading={() => <SplashScreen />}
            style={{ flex: 1 }}
            onMessage={onMessage}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            autoManageStatusBarEnabled={true}
            bounces={false}
            originWhitelist={["*"]}
            scalesPageToFit={true}
            mixedContentMode={"always"}
            allowsInlineMediaPlayback={true}
            allowsFullscreenVideo={true}
            allowsBackForwardNavigationGestures={true}
            allowsLinkPreview={false}
            sharedCookiesEnabled={true}
            domStorageEnabled={true}
            javaScriptEnabled={true}
            javaScriptCanOpenWindowsAutomatically={true}
            source={{ uri: WEB_VIEW_FORM + formVersion }}
          />
          <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "space-around", flex: 0.1 }}>
            <Button style={{ ...styles.btn, backgroundColor: "#F2F5F8" }} labelStyle={{ ...styles.labelStyle, color: "rgba(0, 32, 77, 0.90)" }} label='Lu nhp' size={ButtonSize.medium} />
            <Button
              onPress={handleSubmitForm}
              style={{
                ...styles.btn,
                backgroundColor: disable ? "#A17CBC" : colors.primary
              }}
              labelStyle={{ ...styles.labelStyle, color: "#fff" }} label='Tip tc' size={ButtonSize.medium} />
          </View>
        </>
      )}

    </SafeAreaView>
  )
}

const styles = StyleSheet.create({
  btn: {
    borderRadius: 8,
    minWidth: 160
  },
  labelStyle: {
    fontSize: 14,
    fontWeight: "500"
  }
})

export default InfoContract;