import { BASE_URL_EFLOW, BASE_URL_EREQUEST } from "../utlis/contanst"

export const searchListServices = async (token: string) => {
  const dataBody = {
    "page": 0,
    "size": 10000,
    "status": [
      "display"
    ],
    "textSearch": "Dch v vay",
    "isAdmin": false
  }
  return await fetch(BASE_URL_EREQUEST + 'api/services/search', {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "Authorization": "Bearer" + token
    },
    body: JSON.stringify(dataBody)
  })
    .then((res) => res.json())
    .then(async res => {
      const idService = res.content[0].id
      const dataListService = await fetch(BASE_URL_EREQUEST + `api/work-flows/get-by-serviceId?serviceId=${idService}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "Authorization": "Bearer" + token
        },
      })
        .then((res) => res.json())
        .catch((e) => console.log(e))
      return dataListService;
    })
    .catch((e) => console.log(e))
}

export const getModeler = async (modelerId: string, token: string) => {
  return await fetch(BASE_URL_EFLOW + `api/modelers/${modelerId}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Authorization": "Bearer" + token
    }
  })
    .then((res) => res.json())
    .catch((e) => console.log(e))
}

export const createContract = async (body: any, token: string) => {
  return await fetch(BASE_URL_EREQUEST + `api/tickets`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "Authorization": "Bearer" + token
    },
    body: JSON.stringify(body)
  })
    .then((res) => res.json())
    .catch((e) => console.log(e))
}